.recipe-details {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  background-color: #ffffff;
  border-radius: 10px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  text-align: left;
  font-family: "Roboto", sans-serif;
}

.recipe-header {
  text-align: center;
}

.recipe-image {
  width: 100%;
  max-height: 400px;
  object-fit: cover;
  border-radius: 5px;
}

.recipe-title {
  font-size: 2.5rem;
  color: #2c3e50;
  margin-top: 20px;
}

.recipe-meta {
  display: flex;
  justify-content: space-between;
  margin: 20px 0;
  font-size: 1.1rem;
  font-weight: 500;
  color: #7f8c8d;
}

.recipe-ingredients,
.recipe-instructions {
  margin: 20px 0;
}

.recipe-ingredients h2,
.recipe-instructions h2 {
  font-size: 1.8rem;
  color: #34495e;
  border-bottom: 2px solid #3498db;
  padding-bottom: 5px;
  margin-bottom: 15px;
}

.recipe-ingredients ul {
  list-style-type: square;
  padding-left: 20px;
  margin: 10px 0;
  line-height: 1.6;
  color: #2c3e50;
}

.recipe-instructions div {
  line-height: 1.8;
  color: #2c3e50;
  font-size: 1.1rem;
}

.back-button {
  display: inline-block;
  background-color: #3498db;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 8px;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
  margin-bottom: 20px;
}

.back-button:hover {
  background-color: #2980b9;
  transform: scale(1.05);
}

body.dark-mode .recipe-details {
  background-color: #34495e;
  color: #ecf0f1;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.3);
}

body.dark-mode .recipe-title {
  color: #ecf0f1;
}

body.dark-mode .recipe-meta {
  color: #bdc3c7;
}

body.dark-mode .recipe-ingredients h2,
body.dark-mode .recipe-instructions h2 {
  color: #ecf0f1;
}

body.dark-mode .recipe-ingredients ul {
  color: #ecf0f1;
}

body.dark-mode .recipe-instructions div {
  color: #ecf0f1;
}

body.dark-mode .recipe-card {
  background-color: #2c3e50;
  color: #ecf0f1;
  border: 1px solid #7f8c8d;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.3);
}

body.dark-mode .recipe-card h3 {
  color: #ecf0f1;
}

body.dark-mode .recipe-card img {
  border-radius: 8px;
}

body.dark-mode .dark-mode-toggle {
  background-color: #2980b9;
}

body.dark-mode .dark-mode-toggle:hover {
  background-color: #1f6b99;
}

body.dark-mode .back-button {
  background-color: #2980b9;
}

body.dark-mode .back-button:hover {
  background-color: #1f6b99;
}

body.light-mode .recipe-card {
  background-color: #ffffff;
  color: #333;
  border: 1px solid #ddd;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
}

body.light-mode .recipe-card h3 {
  color: #2c3e50;
}

body.light-mode .recipe-card img {
  border-radius: 8px;
}

body.light-mode .recipe-card:hover {
  background-color: #f9f9f9;
  box-shadow: 0px 6px 12px rgba(0, 0, 0, 0.1);
}

body.light-mode .back-button {
  background-color: #3498db;
}

body.light-mode .back-button:hover {
  background-color: #2980b9;
}
